:root{
    --bsr-margin: 1px;
    --bsr-font-size:18px;
    --bsr-height:30px;
    --bsr-min-width:35px;
    --bsr-color: #090909;
    --bsr-padding:0

}
[data-paginator] button{
    padding: var(--bsr-padding);
    height: var(--bsr-height);
    width: fit-content;
    min-width: var(--bsr-min-width);
    margin: var(--bsr-margin);
    font-size: var(--bsr-font-size);
    font-weight: normal;
    border: none;
    border-radius: 0;
    outline: none;
}

.bsr-paginator {
    display: none;
    width: fit-content;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding: 5px;
}



.bsr-button-page {
    color: #0a708d;
    background-color: #c9eff6;
    cursor: pointer;
}
.bsr-button-page:hover {
    background-color: #9ae5f8;
}
.bsr-button-page:focus {
    background-color: #5799ef;
    color: white;
}

.bsr-button-selection {
    background-color: #5799ef;
    color: white;
}








.bsr-button-ellipsis {
    background: transparent;
    cursor: default;
    color: var(--bsr-color);
}


.bsr-button-navigate {
    color: var(--bsr-color);
    background-color: transparent;
    cursor: pointer;

}
.bsr-button-navigate:hover{
    background-color: #dedcdc;
}
.bsr-button-navigate:active{
    background-color: #c7c6c6;
}
.bsr-button-navigate:disabled{
    background-color: transparent;
    opacity: 50%;
    cursor: not-allowed;
    border: none;
    outline: none;
}
.bsr-button-navigate:focus {
    background-color: #dedcdc;
    border: none;
    outline: none;
}
